import {$, Cash, Component, ComponentOptions} from '@zui/core';
import {formatString} from '@zui/helpers';
import {BatchFormOptions, BatchFormRowData, BatchFormCol} from '../types';

export class BatchForm extends Component<BatchFormOptions> {
    static NAME = 'BatchForm';

    #$tbody?: Cash;

    #template?: HTMLTemplateElement;

    #actions: DocumentFragment | null = null;

    #cols: BatchFormCol[] = [];

    #idSeed = 0;

    #rows: number[] = [];

    static DEFAULT: ComponentOptions<BatchFormOptions> = {
        mode: 'add',
        idKey: 'id',
        i18n: {
            'zh_cn': {
                ditto: '',
                delete: '',
                add: '',
            },
            'zh_tw': {
                ditto: '',
                delete: '',
                add: '',
            },
            en: {
                ditto: 'Ditto',
                delete: 'Delete',
                add: 'Add',
            },
        },
    };

    afterInit() {
        const $ele   = $(this.element);
        const $table = $ele.find('.form-batch-table').addClass('borderless');
        let $tbody   = $table.find('tbody');

        if (!$tbody.length) {
            $tbody = $('<tbody></tbody>').appendTo($table);
        }

        this.#$tbody   = $tbody;
        this.#template = $ele.find('.form-batch-template').get(0) as HTMLTemplateElement;

        this.#cols = [];
        $table.find('thead>tr>.form-batch-head').each((_, th) => {
            const $th = $(th);
            const col  = $th.data();
            if (col) {
                this.#cols.push(col as BatchFormCol);
            }
        });

        $ele.on('click', (event) => {
            const $btn = $(event.target).closest('.form-batch-btn');
            if (!$btn.length) {
                return;
            }
            const type = $btn.data('type');
            const $row = $btn.closest('tr');
            const rowIndex = $row.data('index');
            if (type === 'add') {
                this.addRow(rowIndex);
            } else if (type === 'delete') {
                this.deleteRow(rowIndex);
            } else if (type === 'ditto') {
                this.toggleDitto($btn);
            }
        }).on('change', '.form-batch-input', event => {
            this.syncDitto($(event.target));
        });

        this.render();
    }

    destroy() {
        $(this.element).off('click change');
        this.#$tbody = undefined;
        this.#template = undefined;
        this.#cols.length = 0;
        this.#rows.length = 0;
    }

    render(options?: Partial<ComponentOptions<BatchFormOptions>> | undefined): void {
        super.render(options);

        if (this.#rows.length) {
            this.#renderRows();
        } else {
            this.#actions = null;
            this.#initRows();
        }
    }

    addRow(index?: number) {
        const gid = this.#idSeed++;
        if (typeof index === 'number' && index >= 0 && index <= this.#rows.length) {
            this.#rows.splice(index + 1, 0, gid);
        } else {
            index = this.#rows.length;
            this.#rows.push(gid);
        }
        this.#renderRows(undefined, index);
    }

    deleteRow(index: number) {
        if (this.#rows.length <= 1 || typeof index !== 'number' || index < 0 || index >= this.#rows.length) {
            return false;
        }
        const gid = this.#rows[index];
        this.#rows.splice(index, 1);
        this.#$tbody?.children(`[data-gid="${gid}"]`).remove();
        this.#renderRows(undefined, index);
    }

    deleteRowByGid(gid: number) {
        return this.deleteRow(this.#rows.indexOf(gid));
    }

    toggleDitto($target: Cash, toggle?: boolean) {
        const $td = $target.closest('td');
        toggle = toggle ?? ($td.attr('data-ditto') !== 'on');
        $td.attr('data-ditto', toggle ? 'on' : 'off');
        if (toggle) {
            const $prevRow = $td.closest('tr').prev('tr');
            const $prevTd = $prevRow.find(`td[data-name="${$td.data('name')}"]`);
            $prevTd.find('.form-batch-input').each((_, ele) => {
                const $ele = $(ele);
                const name = $ele.data('name');
                const value = $ele.val();
                this.syncDitto($td.find(`.form-batch-input[data-name="${name}"]`).val(value), false);
            });
        }
    }

    syncDitto($target: Cash, $shouldOff = true) {
        const $td = $target.closest('td');
        if ($shouldOff) {
            $td.attr('data-ditto', 'off');
        }

        const colName = $td.data('name');
        const controlName = $target.data('name');
        const tdSelector = `td[data-name="${colName}"][data-ditto="on"]`;
        const value = $target.val();
        let $nextTr = $target.closest('tr').next('tr');
        let $nextTd = $nextTr.find(tdSelector);
        while ($nextTd.length) {
            $nextTd.find(`.form-batch-input[data-name="${controlName}"]`).val(value);
            $nextTr = $nextTr.next('tr');
            $nextTd = $nextTr.find(tdSelector);
        }
    }

    #initRows() {
        const template = this.#template;
        const $tbody = this.#$tbody;
        if (!template || !$tbody?.length) {
            return;
        }
        const {data = [], minRows, maxRows, mode} = this.options;
        const isAddMode = mode === 'add';
        const rowsCount = isAddMode ? Math.min(Math.max(1, maxRows ?? 100), Math.max(1, 10, minRows ?? 10, data.length)) : data.length;
        this.#rows = Array(rowsCount).fill(0).map((_, idx) => idx);
        this.#idSeed = this.#rows.length;

        this.#renderRows(data);
    }

    #renderRows(dataList: BatchFormRowData[] = [], beginIndex = 0) {
        const count = this.#rows.length;
        for (let i = beginIndex; i < count; i++) {
            this.#renderRow(i, dataList[i]);
        }

        this.#$tbody?.attr('data-count', `${count}`);
    }

    #renderActions($col: Cash) {
        let actions = this.#actions;
        if (!actions) {
            const {addRowIcon = 'icon-plus', deleteRowIcon = 'icon-trash'} = this.options;
            actions = new DocumentFragment();
            const btnTemplate = '<button type="button" data-type="{type}" class="form-batch-btn btn square ghost size-sm" title="{text}"><i class="icon {icon}"></i></button>';
            if (addRowIcon !== false) {
                actions.append($(formatString(btnTemplate, {type: 'add', icon: addRowIcon, text: this.i18n('add')}))[0]!);
            }
            if (deleteRowIcon !== false) {
                actions.append($(formatString(btnTemplate, {type: 'delete', icon: deleteRowIcon, text: this.i18n('delete')}))[0]!);
            }
            this.#actions = actions;
        }
        $col.empty().append(actions.cloneNode(true));
    }

    #renderRow(index: number, data?: BatchFormRowData) {
        const $tbody = this.#$tbody!;
        const gid = String(this.#rows[index]);
        let $row = $tbody.children(`[data-gid="${gid}"]`);
        if (!$row.length) {
            const template = this.#template!;
            const rowElement = document.importNode(template.content, true);
            const trElement = rowElement.querySelector('tr');
            $row = $(trElement).attr('data-gid', gid);
        } else if (!data && $row.data('index') === index) {
            return;
        }
        $row.attr('data-index', `${index}`);

        if (index) {
            const preGid = this.#rows[index - 1];
            const $prevRow = $tbody.children(`[data-gid="${preGid}"]`);
            if ($prevRow.length) {
                $prevRow.after($row);
            } else {
                $row.appendTo($tbody);
            }
        } else {
            $row.prependTo($tbody);
        }

        const {idKey = 'id', mode, onRenderRowCol, onRenderRow} = this.options;
        const isAddMode = mode === 'add';
        const id = String((isAddMode || !data) ? (index + 1) : data[idKey]);
        this.#cols.forEach(col => {
            let $col = $row.find(`td[data-name="${col.name}"]`);
            if (!$col.length) {
                $col = $(`<td data-name="${col.name}"></td>`).appendTo($row);
            }
            if (col.index) {
                $col.find('.form-control-static').text(id).attr('id', `${col.name}_${gid}`);
                onRenderRowCol?.call(this, $col, col, data, index);
                return;
            }

            if (!$col.data('init') || data) {
                if (col.name === 'ACTIONS') {
                    $col.addClass('form-batch-row-actions');
                    if (!isAddMode) {
                        return;
                    }
                    this.#renderActions($col);
                    return;
                }
                $col.data('init', 1)
                    .find('[name],.form-control-static')
                    .each((_, ele) => {
                        const $ele = $(ele);
                        if ($ele.hasClass('form-control-static')) {
                            const name = $ele.attr('data-name')!;
                            $ele.attr('id', `${col.name}_${gid}`);
                            if (data) {
                                $ele.text(String(data[name] ?? ''));
                            }
                        } else {
                            const name = $ele.attr('name')!;
                            const domID = $ele.attr('id')!;
                            $ele.attr({
                                id: `${domID}_${gid}`,
                                name: `${name}[${id}]`,
                                'data-name': name,
                            }).addClass('form-batch-input');
                            $col.find(`label[for="${domID}"]`)
                                .each((_1, e) => {
                                    $(e).attr('for', `${domID}_${gid}`);
                                });

                            if (data) {
                                $ele.val(String(data[name] ?? ''));
                            }

                        }
                    });
            } else {
                $col.find('[name]').each((_, ele) => {
                    const $ele = $(ele);
                    const name = $ele.attr('data-name')!;
                    if ($ele.attr('name')?.startsWith(`${name}[`)) {
                        $ele.attr('name', `${name}[${id}]`);
                    }
                });
            }

            if (col.ditto && !$col.hasClass('form-batch-ditto')) {
                $col.addClass('form-batch-ditto');
                if (index) {
                    const $btn = $(`<div class="input-control-suffix form-batch-ditto-btn"><button type="button" class="btn ghost form-batch-btn" data-type="ditto">${this.i18n('ditto')}</button></div>`);
                    const $control = $('<div class="input-control input-control-ditto has-suffix"></div>')
                        .append($col.children())
                        .append($btn)
                        .appendTo($col);
                    requestAnimationFrame(() => $control.css('--input-control-suffix', `${$btn.find('.btn').outerWidth()}px`));
                    $col.attr('data-ditto', col.defaultDitto ?? 'on');
                } else {
                    $col.attr('data-ditto', '');
                    const $ditto = $col.find('.input-control-ditto');
                    if ($ditto.length) {
                        $ditto.children().not('.form-batch-ditto-btn').appendTo($col);
                        $ditto.remove();
                    }
                }
            }

            onRenderRowCol?.call(this, $col, col, data, index);
        });

        onRenderRow?.call(this, $row, index, data);
    }
}
