import {Component} from 'preact';
import {classes} from '@zui/core';
import {GlobalSearchPanelProps} from '../types';

export class GlobalSearchPanel extends Component<GlobalSearchPanelProps> {
    private renderCommonSearch(className?: string) {
        const {commonSearchText, commonSearchUrl, searchValue} = this.props;
        return (
            <li key="common" className={classes('w-full rounded flex items-center my-0.5', className)}>
                <a className="inline-block p-1 ellipsis" style={{color: 'inherit'}} href={commonSearchUrl.replace('{searchValue}', searchValue)}>
                    {commonSearchText} {searchValue}
                </a>
            </li>
        );
    }

    render() {
        const {searchValue, searchItems, selectedKey} = this.props;
        if (searchValue === '') {
            return null;
        }

        if (!/^\d+$/.test(searchValue)) {
            return (
                <ul className="global-search-panel p-1 rounded shadow bg-white w-full">
                    {this.renderCommonSearch('secondary')}
                </ul>
            );
        }

        return (
            <ul className="global-search-panel flex flex-wrap p-1 rounded shadow bg-white w-full">
                {this.renderCommonSearch()}
                {
                    searchItems.map(({key, text, url}) => (
                        <li key={key} data-fk={key} className={classes({
                            rounded: true,
                            'my-0.5': true,
                            'flex': true,
                            'justify-between': true,
                            'items-center': true,
                            'w-1/2': key !== selectedKey,
                            'w-full': key === selectedKey,
                            secondary: key === selectedKey,
                            'order-first': key === selectedKey,
                        })}>
                            <a className="inline-block p-1 ellipsis" style={{color: 'inherit'}} href={url.replace('{searchValue}', searchValue)}>
                                {text} #{searchValue}
                            </a>
                            {key === selectedKey && <i className="icon icon-check mr-2" />}
                        </li>
                    ))
                }
            </ul>
        );
    }
}
