import {useState} from 'preact/hooks';

type SearchConditionProps = {
    text: string;
    applyURL: string;
    deleteURL: string;
};

export default function SearchCondition(props: SearchConditionProps) {
    const {text, applyURL, deleteURL} = props;
    const [wrapBg, setWrapBg] = useState('lighter');
    const [btnBg, setBtnBg] = useState('lighter');

    return (
        <a
            className={`search-condition label rounded-full h-6 flex p-1 gap-2 items-center cursor-pointer ${wrapBg}`}
            href={applyURL}
            style={{width: 'fit-content', maxWidth: '100%'}}
            onMouseOver={() => setWrapBg('gray')}
            onMouseLeave={() => setWrapBg('lighter')}
        >
            <span className="ellipsis">{text}</span>
            <a
                href={deleteURL}
                className={`rounded-full h-5 w-5 center ${btnBg} shrink-0 grow-0`}
                onMouseOver={() => setBtnBg('danger')}
                onMouseLeave={() => setBtnBg('lighter')}
            >
                <i className="icon icon-close" />
            </a>
        </a>
    );
}
